// æ€§èƒ½ç›‘æ§å·¥å…·
export class PerformanceMonitor {
    private marks: Map<string, number> = new Map();

    // å¼€å§‹è®¡æ—¶
    start(label: string) {
        this.marks.set(label, performance.now());
    }

    // ç»“æŸè®¡æ—¶å¹¶è¿”å›è€—æ—¶
    end(label: string): number {
        const startTime = this.marks.get(label);
        if (!startTime) {
            console.warn(`Performance mark "${label}" not found`);
            return 0;
        }
        
        const duration = performance.now() - startTime;
        this.marks.delete(label);
        
        if (process.env.NODE_ENV === 'development') {
            console.log(`[Performance] ${label}: ${duration.toFixed(2)}ms`);
        }
        
        return duration;
    }

    // æµ‹é‡å‡½æ•°æ‰§è¡Œæ—¶é—´
    async measure<T>(label: string, fn: () => T | Promise<T>): Promise<T> {
        this.start(label);
        try {
            const result = await fn();
            this.end(label);
            return result;
        } catch (error) {
            this.end(label);
            throw error;
        }
    }

    // è·å–é¡µé¢åŠ è½½æ€§èƒ½æŒ‡æ ‡
    getPageMetrics() {
        const navigation = performance.getEntriesByType('navigation')[0] as PerformanceNavigationTiming;
        
        if (!navigation) return null;

        return {
            // DNS æŸ¥è¯¢æ—¶é—´
            dnsTime: navigation.domainLookupEnd - navigation.domainLookupStart,
            // TCP è¿æ¥æ—¶é—´
            tcpTime: navigation.connectEnd - navigation.connectStart,
            // è¯·æ±‚å“åº”æ—¶é—´
            requestTime: navigation.responseEnd - navigation.requestStart,
            // DOM è§£ææ—¶é—´
            domParseTime: navigation.domContentLoadedEventEnd - navigation.domContentLoadedEventStart,
            // é¡µé¢åŠ è½½å®Œæˆæ—¶é—´
            loadTime: navigation.loadEventEnd - navigation.loadEventStart,
            // æ€»æ—¶é—´
            totalTime: navigation.loadEventEnd - navigation.fetchStart,
        };
    }

    // æ‰“å°é¡µé¢æ€§èƒ½æŠ¥å‘Š
    logPageMetrics() {
        const metrics = this.getPageMetrics();
        if (metrics) {
            console.table(metrics);
        }
    }
}

export const perfMonitor = new PerformanceMonitor();

// React æ€§èƒ½ç›‘æ§ Hook
export function usePerformance(label: string) {
    if (process.env.NODE_ENV === 'development') {
        perfMonitor.start(label);
        return () => perfMonitor.end(label);
    }
    return () => {};
}
